.ant-pro-top-nav-header{position:relative;width:100%;height:64px;box-shadow:0 2px 8px rgba(0,0,0,.15);transition:background .3s,width .2s}.ant-pro-top-nav-header .ant-menu-submenu.ant-menu-submenu-horizontal{height:100%;line-height:64px}.ant-pro-top-nav-header .ant-menu-submenu.ant-menu-submenu-horizontal .ant-menu-submenu-title{height:100%}.ant-pro-top-nav-header.light{background-color:#fff}.ant-pro-top-nav-header.light h1{color:#002140}.ant-pro-top-nav-header-main{display:flex;height:64px;padding-left:24px}.ant-pro-top-nav-header-main.wide{max-width:1200px;margin:auto;padding-left:0}.ant-pro-top-nav-header-main .left{display:flex;flex:1 1}.ant-pro-top-nav-header-main .right{width:324px}.ant-pro-top-nav-header-logo{position:relative;width:165px;height:64px;overflow:hidden;line-height:64px;transition:all .3s}.ant-pro-top-nav-header-logo img{display:inline-block;height:32px;vertical-align:middle}.ant-pro-top-nav-header-logo h1{display:inline-block;margin:0 0 0 12px;color:#fff;font-weight:400;font-size:16px;vertical-align:top}.ant-pro-top-nav-header-menu .ant-menu.ant-menu-horizontal{height:64px;line-height:64px;border:none}