.ant-pro-global-header{position:relative;height:64px;padding:0;background:#fff;box-shadow:0 1px 4px rgba(0,21,41,.08)}.ant-pro-global-header-logo{display:inline-block;height:64px;padding:0 0 0 24px;font-size:20px;line-height:64px;vertical-align:top;cursor:pointer}.ant-pro-global-header-logo img{display:inline-block;width:32px;vertical-align:middle}.ant-pro-global-header-menu .anticon{margin-right:8px}.ant-pro-global-header-menu .ant-dropdown-menu-item{min-width:160px}.ant-pro-global-header-trigger{height:64px;padding:19px 24px;font-size:20px;cursor:pointer;transition:all .3s,padding 0s}.ant-pro-global-header-trigger:hover{background:#fff}.ant-pro-global-header .dark{height:64px}.ant-pro-global-header .dark .action{color:hsla(0,0%,100%,.85)}.ant-pro-global-header .dark .action>i{color:hsla(0,0%,100%,.85)}.ant-pro-global-header .dark .action.opened,.ant-pro-global-header .dark .action:hover{background:#1890ff}.ant-pro-global-header .dark .action .ant-badge{color:hsla(0,0%,100%,.85)}